* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

body {
  min-height: 100vh;
  display: flex;
  background: linear-gradient(135deg, #f5f7fa 0%, #ffffff 100%);
}

.container {
  display: flex;
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 20px;
  gap: 40px;
  align-items: center;
  justify-content: center;
}

.login-form {
  background: white;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
  position: relative;
  overflow: hidden;
}

.decoration {
  position: absolute;
  top: -50px;
  right: -50px;
  width: 100px;
  height: 100px;
  background: #ff4b4b;
  border-radius: 50%;
  opacity: 0.1;
}

.decoration:nth-child(2) {
  top: auto;
  bottom: -30px;
  left: -30px;
  width: 60px;
  height: 60px;
  background: #ff6b6b;
}

h1 {
  color: #333;
  margin-bottom: 30px;
  font-size: 28px;
  font-weight: 600;
}

.input-group {
  margin-bottom: 20px;
  position: relative;
}

.input-group input {
  width: 100%;
  padding: 15px;
  border: 2px solid #eee;
  border-radius: 12px;
  font-size: 16px;
  transition: all 0.3s ease;
  background: #f8f9fa;
}

.input-group input:focus {
  border-color: #ff4b4b;
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 75, 75, 0.1);
}

.input-group label {
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: #666;
  transition: all 0.3s ease;
  pointer-events: none;
  font-size: 16px;
}

.input-group input:focus + label,
.input-group input:not(:placeholder-shown) + label {
  top: 0;
  font-size: 12px;
  background: white;
  padding: 0 5px;
}

button {
  width: 100%;
  padding: 15px;
  background: #ff4b4b;
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

button:hover {
  background: #ff3333;
  transform: translateY(-2px);
}

.helper-text {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  font-size: 14px;
}

.helper-text a {
  color: #ff4b4b;
  text-decoration: none;
  transition: all 0.3s ease;
}

.helper-text a:hover {
  color: #ff3333;
  text-decoration: underline;
}

.illustration {
  display: none;
  max-width: 500px;
  width: 100%;
}

@media (min-width: 768px) {
  .illustration {
    display: block;
  }
}

.blood-drop {
  position: relative;
  width: 300px;
  height: 300px;
  margin: auto;
}

.drop {
  position: absolute;
  width: 100px;
  height: 100px;
  background: #ff4b4b;
  border-radius: 0 50% 50% 50%;
  transform: rotate(45deg);
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    transform: rotate(45deg) scale(1);
  }
  50% {
    transform: rotate(45deg) scale(1.1);
  }
  100% {
    transform: rotate(45deg) scale(1);
  }
}
